:root {
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

body {
  margin-left: 20px;
  margin-right: 20px;
  font-family: Roboto, sans-serif;
}

p, h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.breed-select {
  min-width: 150px;
  height: 40px;
  letter-spacing: .04em;
  color: #000;
  cursor: pointer;
  border-radius: 4px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.5;
  display: block;
}

.loader {
  letter-spacing: .04em;
  color: #000;
  margin-top: 20px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.5;
}

.cat-info {
  gap: 20px;
  display: flex;
}

.cat-info-photo {
  width: 600px;
  height: 300px;
  transition: transform var(--transition), box-shadow var(--transition);
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.cat-info-photo:hover {
  transform: translateY(-10px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, .3);
}

.cat-info-photo img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.cat-info-container {
  width: 800px;
  height: 300px;
}

.cat-info-text {
  margin-bottom: 10px;
  font-weight: 600;
}

.cat-info-title {
  margin-bottom: 20px;
}

.cat-info-text span {
  font-weight: 400;
}

